import React, { useState } from 'react';
import './App.scss';
import AppMenu from './component/appMenu/AppMenu';
import HamTaker from "./component/hamTaker/HamTaker";
import Canvas from './component/canvas/App';

function App() {
  const [appState, setAppState] = useState(<Canvas />);

  return (
    <div className="App">
      {appState}
      <AppMenu setAppState={setAppState}/>
    </div>
  );
}

export default App;
